import React, { useEffect, useContext, useState } from "react";

// INTERNAL IMPORT
import { CrowdFundingContext } from "../Context/CrowdFunding"; // Corrected spelling based on context
import { Hero, Card, PupUp } from "../Components";

const index = () => {
  // 1. EXTRACT DATA FROM CONTEXT (CrowdFundingContext)
  const {
    titleData,
    createCampaign,
    donate,
    getUserCampaigns,
    getDonations,
    getCampaigns: getCampaignsData, // Renamed for clarity in useEffect
    // getCampaigns is missing from context images but is used as getCampaignsData
    // in useEffect, assuming 'getCampaigns' was exported as 'getCampaigns' or
    // we should use the one extracted on line 9 (getCampaign) which is now renamed
    getCampaigns: getCampaignsContextFunction, // Assuming a function for all campaigns
  } = useContext(CrowdFundingContext);

  // 2. STATE MANAGEMENT FOR CAMPAIGN DATA AND UI
  const [allcampaign, setAllcampaign] = useState();
  const [usercampaign, setUsercampaign] = useState();

  // DONATE POPUP MODEL STATE
  const [openModel, setOpenModel] = useState(false);
  const [donateCampaign, setDonateCampaign] = useState(); // Holds the campaign object for donation

  // 3. EFFECT TO FETCH CAMPAIGN DATA ON MOUNT
  useEffect(() => {
    // These calls likely return promises to fetch the campaign functions
    const getCampaigns = getCampaignsContextFunction(); 
    const getUserCampaignsFunction = getUserCampaigns();

    // Inner async function to resolve promises and update state
    const fetchData = async () => {
      // Assuming getCampaigns and getUserCampaigns return the functions, 
      // not the data directly based on the line 20-21 in one image
      // Let's adjust to the more common pattern seen in the images (lines 20-23 and 24-26)
      const campaignsPromise = getCampaignsContextFunction();
      const userCampaignsPromise = getUserCampaigns();
      
      const allData = await campaignsPromise;
      const userData = await userCampaignsPromise;

      setAllcampaign(allData);
      setUsercampaign(userData);
    };

    fetchData();
  }, []); // Empty dependency array means this runs once after the initial render

  // Optional: Console log to verify state (based on image f15371.png)
  // console.log(donateCampaign); 

  // 4. COMPONENT RENDER
  return (
    <>
      <Hero titleData={titleData} createCampaign={createCampaign} />

      {/* Card for All Listed Campaigns */}
      <Card
        title="All Listed Campaign"
        allcampaign={allcampaign}
        setOpenModel={setOpenModel} // Passed to open the donation model
        setDonate={setDonateCampaign} // Passed to set the campaign object to be donated to
      />

      {/* Card for User's Created Campaigns */}
      <Card
        title="Your Created Campaign"
        allcampaign={usercampaign}
        setOpenModel={setOpenModel} // Passed to open the donation model
        setDonate={setDonateCampaign} // Passed to set the campaign object to be donated to
      />

      {/* Conditional Popup Model for Donation */}
      {openModel && donateCampaign && (
        <PupUp
          setOpenModel={setOpenModel} // To close the model
          getDonations={getDonations} // To fetch current donations for the campaign
          donate={donateCampaign} // The campaign object being donated to
          donateFunction={donate} // The function to execute the donation transaction
        />
      )}
    </>
  );
};

export default index;